import React from 'react'

export const SelectFour = (props) => {


    const choisirFour = (event) =>{
      props.callback(event.target.value)
    }

    return (
      <div className="form-group">
        <label htmlFor="four">Four choisi</label>
        <select className="form-control form-control-lg" defaultValue="DEFAULT" onChange={choisirFour} id="fourSelect">
                 <option value="DEFAULT"   disabled>Choisissez le four</option>
                <option value="mini">Minidiamant</option>
                <option value="c1">Consarc 1</option>
                <option value="c2">Consarc 2</option>
                <option value="b53">B53</option>
                <option value="b54">B54</option>
                <option value="tth">TTH</option>
        </select>
      </div>
    )
}